.title {
  margin: 0;
  font-family: 'IgraSans', sans-serif;
  font-weight: 400;
  font-size: 46px;
  line-height: 109%;
  text-align: center;
  text-transform: uppercase;
  color: var(--primary-color);

  @include small-tablet {
    font-size: 32px;
  }

  @include mobile {
    font-size: 26px;
  }
}

.descr {
  font-weight: 400;
  font-size: 18px;
  line-height: 156%;
  text-align: center;
  color: var(--primary-color);

  @include mobile {
    font-size: 16px;
  }
}
