.team {
  padding-top: 115px;
  padding-bottom: 80px;
  background-color: var(--grey-color);

  @include medium-tablet {
    padding-top: 90px;
    padding-bottom: 70px;
  }

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 60px;
  }

  &__heading {
    margin-bottom: 60px;

    @include medium-tablet {
      margin-bottom: 50px;
    }

    @include mobile {
      margin-bottom: 35px;
    }
  }

  &__title {
    position: relative;
    margin-bottom: 30px;

    @include small-tablet {
      margin-bottom: 20px;
    }

    &::before {
      position: absolute;
      content: "Команда";
      left: 50%;
      top: -35px;
      transform: translateX(-50%);
      font-family: 'Igra Sans', sans-serif;
      font-size: 68px;
      line-height: 103%;
      text-transform: uppercase;
      background: linear-gradient(180deg, #525556 0%, rgba(82, 85, 86, 0) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      opacity: 0.1;

      @include small-tablet {
        left: 50%;
        transform: translateX(-50%);
        top: -30px;
        width: 100%;
        font-size: 52px;
      }

      @include mobile {
        font-size: 36px;
        top: -25px;
      }
    }
  }

  &__item {
    text-align: center;
  }

  &__image {
    border-radius: 10px;
    margin-bottom: 30px;
    text-align: center;
    justify-content: center;

    @include small-tablet {
      margin-bottom: 20px;
    }

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__name {
    margin: 0;
    margin-bottom: 10px;
    font-family: 'IgraSans', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 133%;
    text-align: center;
    text-transform: uppercase;
    color: var(--primary-color);

    @include small-tablet {
      margin-bottom: 5px;
      font-size: 16px;
    }
  }

  &__special {
    font-weight: 400;
    font-size: 16px;
    line-height: 138%;
    text-align: center;
    color: var(--primary-color);

    @include mobile {
      font-size: 14px;
    }
  }

  .swiper-slide {
    width: 362px;
  }

  .swiper-wrapper {
    margin-bottom: 60px;

    @include small-tablet {
      margin-bottom: 45px;
    }

    @include mobile {
      margin-bottom: 35px;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__slider-btn {
    @include bg-cover;

    width: 70px;
    height: 70px;
    border-radius: 100%;
    transition: box-shadow 0.3s ease-in;

    @include small-tablet {
      width: 50px;
      height: 50px;
    }

    &--prev {
      background-image: url("../img/team/prev.svg");
    }

    &--next {
      background-image: url("../img/team/next.svg");
    }

    &:hover {
      box-shadow: 0px 2px 15px rgba(82, 85, 86, 0.55);
    }
  }
}

.swiper-pagination {
  bottom: 20px !important;
  left: 25% !important;
  width: 50% !important;

  @include small-tablet {
    bottom: 15px !important;
  }
}

.swiper-pagination-bullet {
  width: 12px !important;
  height: 12px !important;
  background: transparent !important;
  border: 1px solid #525556 !important;
  opacity: 1 !important;

  @include small-tablet {
    width: 8px !important;
    height: 8px !important;
  }
}

.swiper-pagination-bullet-active {
  background: #525556 !important;
}

