.header {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  padding: 35px 0;
  width: 100%;
  z-index: 100;

  @include mobile {
    padding: 13px 0;
    background-color: var(--light-color);
  }

  .logo {
    width: 196px;
    height: 40px;

    @include mobile {
      width: 167px;
      height: 34px;
    }
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__contacts {
    margin-top: 5px;
    display: flex;
    flex-direction: column;

    @include mobile {
      display: none;
    }
  }

  &__phone {
    margin-bottom: 5px;
    position: relative;
    font-family: 'Igra Sans', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-align: right;
    color: var(--accent-color);
    transition: color 0.3s ease-in, text-decoration 0.3s ease-in;

    &::before {
      position: absolute;
      content: "";
      left: -42px;
      top: 50%;
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
      background-image: url('../img/phone-icon.svg');
      background-repeat: no-repeat;
      background-size: contain;
      transition: left 0.3s ease-in;
    }

    &:hover {
      color: var(--secondary-color);

      &::before {
        left: -37px;
      }
    }
  }

  &__email {
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    text-align: right;
    color: var(--primary-color);
    transition: font-weight 0.3s ease-in, text-decoration 0.3s ease-in;

    &:hover {
      font-weight: 500;
      text-decoration: underline;
    }
  }
}

.nav {
  display: flex;
  align-items: center;

  @media (max-width: 930px) {
    display: none;
  }

  @include mobile {
    display: flex;
    padding: 30px 0;
    position: fixed;
    left: 0;
    top: 60px;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: auto;
    background-color: var(--light-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &__list {
    display: flex;
    align-items: center;

    @include mobile {
      flex-direction: column;
      justify-content: center;
      width: 100%;
    }
  }

  &__item:not(:last-child) {
    margin-right: 30px;

    @media (max-width: 1150px) {
      margin-right: 25px;
    }

    @include mobile {
      margin-right: 0;
      margin-bottom: 15px;
    }
  }

  @media (max-width: 1150px) {
    &__item:nth-child(1) {
      display: none;
    }
  }

  @include mobile {
    &__item:nth-child(1) {
      display: block;
    }
  }

  &__link {
    position: relative;
    font-weight: 300;
    font-size: 15px;
    line-height: 115.3%;
    color: var(--primary-color);
    transition: border-bottom 0.3s ease-in, before 0.3s ease-in;

    @media (max-width: 1150px) {
      font-size: 14px;
    }

    &:hover {
      font-weight: 500;
      &::before {
        position: absolute;
        content: "";
        left: 0;
        right: 0;
        bottom: -10px;
        width: 100%;
        height: 2px;
        background-color: var(--accent-color);
      }
    }
  }
}

.burger {
  @include burger;
  z-index: 501;

  @media (min-width: 576px) {
    display: none;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
