.contacts {
  position: relative;
  min-height: 600px;

  @include mobile {
    min-height: 500px;
  }

  &__container {
    padding-top: 76px;

    @include mobile {
      padding-top: 60px;
      padding-bottom: 60px;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    padding: 30px;
    max-width: 382px;
    background-color: var(--light-color);
    border-radius: 10px;

    @include mobile {
      max-width: 100%;
      align-items: center;
      text-align: center;
    }
  }

  .map {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: #EEEEEE;
    z-index: -1;
  }

  [class*="copyrights-pane"] {
    display: none !important;
  }

  // [class*="ground-pane"] {
  //   filter: grayscale(1);
  // }

  &__item:not(:last-child) {
    position: relative;
    margin-bottom: 30px;

    &::before {
      position: absolute;
      content: "";
      left: 0;
      right: 0;
      bottom: -15px;
      width: 100%;
      height: 1px;
      background-color: #EEEEEE;
      border-radius: 10px;
    }
  }

  &__item-descr {
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--primary-color);

    span {
      color: var(--accent-color);
    }
  }

  &__item-title {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    color: var(--primary-color);
  }

  &__item-phone {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      justify-content: center;
      flex-direction: column;
    }

    &--item {
      font-family: 'IgraSans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;
      color: #525556;

      @include mobile {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
    }
  }

  &__item-email, &__item-address {
    font-family: 'IgraSans', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #525556;
  }

  &__item-button {
    padding-top: 5px;
  }

  .consultation__btn {
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--primary-color);
    border-bottom: 1px dashed var(--primary-color);
    transition: color 0.3s ease-in, border-bottom 0.3s ease-in;

    &:hover {
      color: var(--accent-color);
      border-bottom: 1px solid var(--accent-color);
    }
  }
}


