// "IgraSans", sans-serif;

// colors
:root {
  // base
  --font-family: "Gilroy", sans-serif;
  --content-width: 1146px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --primary-color: #585858;
  --accent-color: #F7AB59;
  --secondary-color: #E39745;
  --grey-color: #EEEEEE;
  --light-color: #ffffff;
}
