.hero {
  position: relative;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  min-height: 780px;
  background-color: var(--grey-color);

  @include medium-tablet {
    min-height: 800px;
  }

  @include mobile {
    min-height: 750px;
  }

  &::before {
    position: absolute;
    content: "";
    right: 40px;
    bottom: 0;
    width: 780px;
    height: 680px;
    background-image: url('../img/hero/hero-img.png');
    background-repeat: no-repeat;
    background-size: contain;

    @media (max-width: 1350px) {
      right: -50px;
    }

    @media (max-width: 1170px) {
      width: 688px;
      height: 600px;
      right: -80px;
    }

    @include medium-tablet {
      width: 528px;
      height: 460px;
    }

    @include small-tablet {
      width: 459px;
      height: 400px;
    }

    @include mobile {
      left: 50%;
      transform: translateX(-50%);
      width: 402px;
      height: 350px;
    }

    @media (max-width: 340px) {
      width: 367px;
      height: 320px;
    }
  }

  &::after {
    position: absolute;
    content: "";
    left: 50%;
    right: 50%;
    bottom: -30px;
    width: 46px;
    height: 60px;
    background-image: url('../img/hero/mouse.svg');
    background-repeat: no-repeat;
    z-index: 5;
  }

  &__content {
    padding-top: 200px;

    @media (max-width: 1170px) {
      padding-top: 150px;
    }

    @include mobile {
      padding-top: 110px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 45px;
    font-family: 'Igra Sans', sans-serif;
    font-weight: 400;
    font-size: 54px;
    line-height: 130%;
    color: var(--primary-color);

    @include small-tablet {
      margin-bottom: 35px;
      font-size: 42px;
    }

    @include mobile {
      margin-bottom: 25px;
      font-size: 28px;
    }

    span {
      color: var(--accent-color);
    }
  }

  &__subtitle {
    position: relative;
    margin: 0;
    margin-bottom: 127px;
    padding-left: 70px;
    font-family: 'Igra Sans', sans-serif;
    font-weight: 400;
    font-size: 22px;
    line-height: 127%;
    color: var(--primary-color);

    @include medium-tablet {
      margin-bottom: 50px;
    }

    @include small-tablet {
      margin-bottom: 40px;
      font-size: 20px;
    }

    @include mobile {
      margin-bottom: 30px;
      padding-left: 50px;
      font-size: 18px;
    }

    &::before {
      position: absolute;
      content: "";
      left: 0;
      top: 13px;
      width: 50px;
      height: 1px;
      background-color: #525556;

      @include mobile {
        top: 10px;
        width: 35px;
      }
    }
  }
}
