.services {
  position: relative;
  padding-top: 115px;
  padding-bottom: 80px;
  background-color: var(--grey-color);
  justify-content: center;

  @include medium-tablet {
    padding-top: 90px;
    padding-bottom: 70px;
  }

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 60px;
  }

  &::before {
    position: absolute;
    content: "";
    left: 0;
    bottom: 205px;
    width: 197px;
    height: 1288px;
    background-image: url(../img/services/left-decor.png);
    background-repeat: no-repeat;

    @media (max-width: 1280px) {
      display: none;
    }
  }

  &::after {
    position: absolute;
    content: "";
    right: 0;
    top: 0;
    width: 350px;
    height: 1500px;
    background-image: url(../img/services/right-decor.png);
    background-repeat: no-repeat;

    @media (max-width: 1280px) {
      display: none;
    }
  }

  &__heading {
    margin-bottom: 70px;

    @include medium-tablet {
      margin-bottom: 50px;
    }

    @include mobile {
      margin-bottom: 35px;
    }
  }

  &__title {
    position: relative;
    margin-bottom: 30px;

    @include small-tablet {
      margin-bottom: 20px;
    }

    &::before {
      position: absolute;
      content: "Услуги";
      left: 50%;
      top: -35px;
      transform: translateX(-50%);
      font-family: 'Igra Sans', sans-serif;
      font-size: 68px;
      line-height: 103%;
      text-transform: uppercase;
      background: linear-gradient(180deg, #525556 0%, rgba(82, 85, 86, 0) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      opacity: 0.1;

      @include small-tablet {
        left: 50%;
        transform: translateX(-50%);
        top: -30px;
        width: 100%;
        font-size: 52px;
      }

      @include mobile {
        font-size: 36px;
        top: -25px;
      }
    }
  }

  &__descr {
    span {
      font-weight: 700;
      color: var(--accent-color);
    }
  }

  &__cards {
    margin-bottom: 30px;

    @include mobile {
      margin-bottom: 15px;
    }
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    padding: 45px 50px;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 10px;

    @include small-tablet {
      padding: 35px 35px;
    }

    @include mobile {
      padding: 25px 15px;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    &:nth-of-type(even) {
      background-color: transparent;
    }

    &-img {
      margin-right: 54px;
      min-width: 160px;
      min-height: 160px;

      @include small-tablet {
        margin-right: 30px;
      }

      @include mobile {
        margin-right: 0;
        margin-bottom: 20px;
      }

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-content {
      max-width: 686px;

      @include small-tablet {
        max-width: 100%;
      }

      @include mobile {
        text-align: center;
      }
    }

    &-title {
      margin: 0;
      margin-bottom: 12px;
      font-family: 'Igra Sans', sans-serif;
      font-weight: 400;
      font-size: 34px;
      line-height: 118%;
      text-transform: uppercase;
      color: var(--primary-color);

      @include small-tablet {
        margin-bottom: 8px;
        font-size: 26px;
      }

      @include mobile {
        font-size: 22px;
        text-align: center;
      }
    }

    &-descr {
      margin-bottom: 17px;
      line-height: 144%;
      color: var(--primary-color);

      @include small-tablet {
        font-size: 16px;
        margin-bottom: 5px;
      }

      @include mobile {
        text-align: center;
      }
    }

    &-link {
      font-weight: 500;
      font-size: 16px;
      line-height: 150%;
      color: var(--primary-color);
      border-bottom: 1px dashed var(--primary-color);
      transition: color 0.3s ease-in, border-bottom 0.3s ease-in;

      @include mobile {
        display: inline-block;
        text-align: center;
      }

      &:hover {
        color: var(--accent-color);
        border-bottom: 1px solid var(--accent-color);
      }
    }

    &-letter {
      position: absolute;
      right: 0;
      top: 0;

      @include mobile {
        display: none;
      }
    }
  }

  &__button {
    text-align: center;
  }
}
