.clinic {
  padding-top: 115px;
  padding-bottom: 80px;

  @include medium-tablet {
    padding-top: 90px;
    padding-bottom: 70px;
  }

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 60px;
  }

  &__heading {
    margin-bottom: 80px;

    @include medium-tablet {
      margin-bottom: 50px;
    }

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__title {
    position: relative;
    margin-bottom: 25px;

    @include small-tablet {
      margin-bottom: 20px;
    }

    span {
      color: var(--accent-color);
    }

    &::before {
      position: absolute;
      content: "О клинике";
      left: 50%;
      top: -35px;
      transform: translateX(-50%);
      font-family: 'Igra Sans', sans-serif;
      font-size: 68px;
      line-height: 103%;
      text-transform: uppercase;
      background: linear-gradient(180deg, #525556 0%, rgba(82, 85, 86, 0) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      opacity: 0.1;

      @include small-tablet {
        left: 50%;
        transform: translateX(-50%);
        top: -30px;
        width: 100%;
        font-size: 52px;
      }

      @include mobile {
        font-size: 36px;
        top: -25px;
      }
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;
    row-gap: 70px;

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 45px;
    }

    @include small-tablet {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 45px;
    }
  }

  &__item {
    text-align: center;

    &-img {
      margin-bottom: 20px;

      @include mobile {
        margin-bottom: 10px;
      }
    }

    &-descr {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: var(--primary-color);
    }
  }
}
