.cta {
  padding-top: 115px;
  padding-bottom: 80px;

  @include medium-tablet {
    padding-top: 90px;
    padding-bottom: 70px;
  }

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 60px;
  }

  &__heading {
    margin-bottom: 60px;

    @include medium-tablet {
      margin-bottom: 50px;
    }

    @include mobile {
      margin-bottom: 35px;
    }
  }

  &__title {
    position: relative;
    margin-bottom: 30px;

    @include small-tablet {
      margin-bottom: 20px;
    }

    &::before {
      position: absolute;
      content: "КОНСУЛЬТАЦИЯ";
      left: 50%;
      top: -35px;
      transform: translateX(-50%);
      font-family: 'Igra Sans', sans-serif;
      font-size: 68px;
      line-height: 103%;
      text-transform: uppercase;
      background: linear-gradient(180deg, #525556 0%, rgba(82, 85, 86, 0) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      opacity: 0.1;

      @include small-tablet {
        left: 50%;
        transform: translateX(-50%);
        top: -30px;
        width: 100%;
        font-size: 52px;
      }

      @include mobile {
        font-size: 36px;
        top: -25px;
      }
    }
  }

  &__descr {
    span {
      font-weight: 600;
      color: var(--accent-color);
    }
  }

  .form {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;
    width: 100%;

    @include medium-tablet {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 20px;
    }

    &__label {
      position: relative;
      width: 100%;

      &--name::before {
        position: absolute;
        content: "";
        top: 25px;
        right: 25px;
        width: 30px;
        height: 30px;
        background-image: url('../img/form-name.svg');
        background-repeat: no-repeat;
        background-size: contain;

        @include mobile {
          top: 20px;
          right: 20px;
          width: 24px;
          height: 24px;
        }
      }

      &--phone::before {
        position: absolute;
        content: "";
        top: 25px;
        right: 25px;
        width: 30px;
        height: 30px;
        background-image: url('../img/form-phone.svg');
        background-repeat: no-repeat;
        background-size: contain;

        @include mobile {
          top: 20px;
          right: 20px;
          width: 24px;
          height: 24px;
        }
      }
    }

    &__input {
      padding: 31px 60px 31px 30px;
      font-weight: 500;
      font-size: 18px;
      line-height: 18px;
      color: var(--primary-color);
      width: 100%;
      height: 80px;
      background-color: rgba($color: #eeeeee, $alpha: 0.5);

      @include mobile {
        font-size: 16px;
        line-height: 16px;
        padding: 28px 45px 28px 20px;
        height: 64px;
      }

      &::placeholder {
        font-weight: 300;
        font-size: 16px;
      }
    }

    &__btn {
      width: 100%;
      padding: 30px 30px 29px 30px;

      @include mobile {
        padding: 23px 30px 22px 30px;
        font-size: 14px;
        line-height: 18px;
      }
    }
  }


}
