.about {
  padding-top: 75px;
  padding-bottom: 80px;

  @include medium-tablet {
    padding-top: 90px;
    padding-bottom: 70px;
  }

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 60px;
  }

  &__container {
    display: flex;
    align-items: center;

    @media (max-width: 1100px) {
      flex-direction: column-reverse;
      align-items: flex-start;
    }
  }

  &__content {
    margin-top: 50px;
    max-width: 588px;

    @media (max-width: 1100px) {
      max-width: 100%;
      margin-top: 0;
      margin-bottom: 50px;
    }

    @include small-tablet {
      margin-bottom: 40px;
    }

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__title {
    position: relative;
    text-align: left;
    font-size: 38px;
    line-height: 132%;
    margin-bottom: 35px;

    @media (max-width: 1100px) {
      max-width: 690px;
    }

    @include small-tablet {
      text-align: center;
      font-size: 32px;
      margin-bottom: 25px;
    }

    @include mobile {
      font-size: 26px;
    }

    &::before {
      position: absolute;
      content: "Special оne";
      left: -3px;
      top: -35px;
      font-family: 'Igra Sans', sans-serif;
      font-size: 58px;
      line-height: 121%;
      text-transform: uppercase;
      background: linear-gradient(180deg, #525556 0%, rgba(82, 85, 86, 0) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      opacity: 0.1;

      @include small-tablet {
        left: 50%;
        transform: translateX(-50%);
        top: -30px;
        width: 100%;
        font-size: 52px;
      }

      @include mobile {
        font-size: 36px;
        top: -25px;
      }
    }
  }

  &__list {

  }

  &__item {
    display: flex;
    flex-direction: row;

    &:not(:last-child) {
      margin-bottom: 20px;

      @include mobile {
        margin-bottom: 15px;
      }
    }

    &-number {
      margin-right: 15px;
      font-family: 'Igra Sans', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 156%;
      color: var(--accent-color);

      @include mobile {
        margin-right: 10px;
        font-size: 16px;
      }
    }

    &-descr {
      text-align: left;
    }
  }

  &__img {
    margin-right: 30px;
    margin-left: -62px;

    @media (max-width: 1100px) {
      margin-left: 0;
      margin-right: 0;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
