.footer {
  padding: 40px 0;
  background-color: var(--primary-color);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
    }
  }

  &__logo {
    width: 196px;
    height: 40px;

    @include small-tablet {
      width: 167px;
      height: 34px;
    }

    @include mobile {
      width: 196px;
      height: 40px;
      margin-bottom: 20px;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;

    @include mobile {
      align-items: center;
      text-align: center;
    }
  }


  &__phone {
    margin-bottom: 5px;
    position: relative;
    font-family: 'Igra Sans', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-align: right;
    color: var(--accent-color);
    transition: color 0.3s ease-in, text-decoration 0.3s ease-in;

    &::before {
      position: absolute;
      content: "";
      left: -42px;
      top: 50%;
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
      background-image: url('../img/phone-icon.svg');
      background-repeat: no-repeat;
      transition: left 0.3s ease-in;

      @include small-tablet {
        display: none;
      }
    }

    &:hover {
      color: var(--secondary-color);

      &::before {
        left: -37px;
      }
    }
  }

  &__email {
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    text-align: right;
    color: var(--light-color);
    transition: font-weight 0.3s ease-in, text-decoration 0.3s ease-in;

    &:hover {
      font-weight: 500;
      text-decoration: underline;
    }
  }

  &__copy {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    text-align: center;
    color: var(--light-color);

    @include small-tablet {
      font-size: 14px;
    }

    @include mobile {
      margin-bottom: 20px;
      font-size: 16px;
    }
  }
}
